<template>
  <div class="about w3-padding">
    <h4> </h4> 
    <el-button @click="rand_zag()"> </el-button>
    <!-- <el-button @click="test()"></el-button> -->
    <!-- <el-button @click="view()">qwe</el-button> -->
    <el-button @click="get_a()"> </el-button>
    <el-row>
        <el-col v-for="el in arr_view" :key="el.id" style="margin: 3px;min-height: 150px;" :span="7" >
           <div :class="'container '+mas[el-1].bg" :style="'display:'+mas[el-1].display" @click="select(mas[el-1])">
             <!-- <p v-if="mas[arr_view[el-1]].isA">{{ mas[arr_view[el-1]].true_a}}</p>
             <p v-else>{{ mas[arr_view[el-1]].q}}</p> -->
              <!-- {{ cont_view(mas[el-1]) }} -->
              <p v-if="mas[el-1].isA">{{ mas[el-1].true_a }}</p>
              <p v-else>{{ mas[el-1].q }} ?</p>
          </div>
        </el-col>
        
    </el-row>
    
    
  </div>
</template>
<script>
 import {store} from '../store'
export default {
  data() {
     return{
        mas:[],
        arr_view:[], //   12 
        col:0,
        sel_mas:[]  //    
     }
  },
  computed:{
    zag(){
      return store.state.zag
    },

  }
  ,
  methods:{
    test(){
      const randomNumber = Math
                .floor(Math.random() * 12) + 1
      let el =this.mas[randomNumber]
      console.log(el)
      this.mas.shift(el)
    },
    cont_view(el){
       console.log(el.q)
       console.log(el.isA)
    },
    rand_view(){   //   12  
      this.arr_view=[]
      do {
            const randomNumber = Math
                .floor(Math.random() * 12) + 1
            if (!this.arr_view.includes(randomNumber)) {
              this.arr_view.push(randomNumber);
            }
        } 
        while (this.arr_view.length <12 );
    },
    rand_zag(){   //   6    
       this.mas = []
        
        do {
            const randomNumber = Math
                .floor(Math.random() * 70) + 1
            if (this.mas.length!=0){
            
                if (!this.mas.includes( {
                                    id:this.zag[randomNumber].id,
                                    isA:false,
                                    display:'block',
                                    bg:'aqua',
                                    q:this.zag[randomNumber].q,
                                    true_a:this.zag[randomNumber].true_a,
                                    hint:this.zag[randomNumber].hint,
                                    })) 
            {
              this.mas.push(
                          {
                            id:this.zag[randomNumber].id,
                            isA:false,
                            bg:'aqua',
                            display:'block',
                            q:this.zag[randomNumber].q,
                            true_a:this.zag[randomNumber].true_a,
                            hint:this.zag[randomNumber].hint,
                          }  
              );
            }}
            else {
              this.mas.push(
                          {
                            id:this.zag[randomNumber].id,
                            isA:false,
                            bg:'aqua',
                            display:'block',
                            q:this.zag[randomNumber].q,
                            true_a:this.zag[randomNumber].true_a,
                            hint:this.zag[randomNumber].hint,
                          }  
              );
            }
        } 
        while (this.mas.length <6 );
        
        
        
    },
    get_a(){   //      mas 
       for (let i in this.mas){
         
        
              this.mas.push({  
                  id:this.mas[i].id+'a',
                  isA:true,
                  bg:'green',
                  display:'block',
                  q:this.mas[i].q,
                  true_a:this.mas[i].true_a,
                  hint:this.mas[i].hint,
                })
       }    
       this.rand_view()
    },
    view(){  //   
      
      console.log(this.arr_view)
      console.log(this.mas)
    }
    ,
    select(el){   //    
      console.log(this.sel_mas)
      if (!el.isA){   //        
        this.sel_mas = []
        for (let a in this.mas){
                if (!this.mas[a].isA){
                  this.mas[a].bg = 'aqua'
                }
              }
        this.sel_mas.push({true_a:el.true_a,id:el.id})
        this.mas.filter((n)=>{    //  
            return n.id.match(el.id)
          })[0].bg = 'selected'
      } else {        //      
        if (this.sel_mas.length<2){
          this.sel_mas.push({true_a:el.true_a,id:el.id})
          if (this.sel_mas[0].true_a==this.sel_mas[1].true_a){
            this.$notify({
                title: 'Success',
                message: ' ',
                type: 'success'
              });
              for (let m in this.mas){
                this.mas.filter((n)=>{    //  
                    return n.id.match(this.sel_mas[m].id)
                  })[0].display = 'none'
              }
              for (let a in this.mas){
                if (!this.mas[a].isA){
                  this.mas[a].bg = 'aqua'
                }
              }
          } else {
            this.$notify({
                title: 'Eroor',
                message: '  ',
                type: 'error'
              });
              for (let a in this.mas){
                if (!this.mas[a].isA){
                  this.mas[a].bg = 'aqua'
                }
              }
          }
        }
         
        
      }

      


      
         
    }
  }
}
</script>
<style>
   .container{
    min-height: 150px; 
    padding: 10px;
    
    border-radius: 10px;
    cursor: pointer;
   }
   .aqua{
    background-color:aquamarine;
   }
   .selected{
    background-color: chocolate;
   }
   .green{
    background-color: bisque;
   }

</style>